import React, { useState } from 'react';
import "../assets/styles/Login.css";
import {Link,  useNavigate} from 'react-router-dom';
import { getAuth, signInWithPopup, GoogleAuthProvider, getRedirectResult, signInWithRedirect, onAuthStateChanged } from "firebase/auth";
import { getDatabase, ref, set, get, child } from "firebase/database";
import Spinner from './Spinner';
function Login() {

  return (
    <main class="form-signin w-100 m-auto text-center mt-5 ">
      <form>
        {/* <img class="mb-4" src={logo} alt="" width="72" height="57"/> */}
        <h1 class="h3 mb-5 fw-normal text-primary ">Log in</h1>

        {/* <div class="form-floating shadow">
          <input
            type="email"
            class="form-control"
            id="floatingInput"
            placeholder="name@example.com" value={email} onChange={(e) => setEmail(e.target.value)}
          />
          <label for="floatingInput">Email address</label>
        </div>
        <div class="form-floating shadow">
          <input
            type="password"
            class="form-control"
            id="floatingPassword"
            placeholder="Password" value={password} onChange={(e) => setPassword(e.target.value)}
          />
          <label for="floatingPassword">Password</label>
        </div>
        <button class="btn btn-primary w-100 py-2 shadow" type="button" >
          Sign in
        </button>
        <p className="text-center text-primary mt-3">or </p> */}

        <div className="d-flex justify-content-center">
        <GoogleBtn />
        </div>
       
      </form>
    </main>
  );
}

const GoogleBtn = () => {
  const navigate = useNavigate();
  const [d,setD]=useState(false)

    function SignInGoogle(){   
      const provider = new GoogleAuthProvider();
      const auth = getAuth();
      signInWithRedirect(auth, provider);
      setD(true)
            
}

const auth = getAuth();
const db = getDatabase();

onAuthStateChanged(auth, (user) => {
  if (user) {
 let uid=user.uid;

 const dbRef = ref(db);

get(child(dbRef,'users/' + uid)).then((snapshot) => {
  if (snapshot.exists()) {
    // console.log(snapshot.val());
    navigate('/user-dashboard');
    setD(false)

  } else {
    console.log("New user");
    set(ref(db, "users/" + uid), {
            name: user.displayName,
            email: user.email,
            pp: user.photoURL,
            uid: user.uid,
            username:user.uid,
            ansq_count:0,
            askq_count:0,
            points: 0
            
          }).then(() => {
             navigate('/user-dashboard');
             setD(false)
          });    
  }
}).catch((error) => {
  console.error(error);
});

  console.log(user)
  navigate('/user-dashboard');
  } 

});
    return (
      <div className="d-flex justify-content-center shadow">
        <button
          className="btn-primary d-flex btn p-0 pe-2 border-0 align-items-center"
          type="button" onClick={SignInGoogle} >
          <div className="bg-light p-2 rounded-start">
            <img
              width="40px"
              src="data:image/png;base64,"
            />
          </div>
          <div className="ms-2">Continue with Google</div>
        </button>

        {!d ? <Spinner display='d-none'/> : <Spinner display='d-block'/>}
      </div>
    );
  };
  

export default Login;
